
@import url(https://fonts.googleapis.com/css2?family=VT323&display=swap);




body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'VT323', 'VT323', 'VT323',
    'VT323', 'VT323', 'VT323', 'VT323', 'VT323', VT323,
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #ffffff;
  color: #000000;
}

code {
  font-family: source-code-pro, VT323, VT323, VT323, 'VT323',
    monospace;
}

.module {
  cursor: url('https://www.solstains.xyz/assets/custom.png'), auto;	
}